#tree[data-v-342aec24] {
  display: block;
  position: relative;
  padding: 5px 15px;
}
#tree ul[data-v-342aec24] {
  position: relative;
  padding-left: 60px;
  margin: 0;
}
#tree ul > li[data-v-342aec24] {
  position: relative;
  padding: 5px 0;
}
#tree > ul[data-v-342aec24] {
  padding: 0;
  margin: 0;
}
/* 水平线 */
#tree > ul ul > li[data-v-342aec24]:after {
  content: "  ";
  position: absolute;
  top: 30px;
  left: -45px;
  width: 45px;
  border-top: 1px solid #ddd;
}
/** 垂直方向连线 */
#tree ul > li[data-v-342aec24]:not(:last-child):before {
  content: " ";
  position: absolute;
  top: 0;
  left: -45px;
  height: 100%;
  border: none;
  border-left: 1px solid #ddd;
}
#tree ul > li[data-v-342aec24]:last-child:before {
  content: " ";
  position: absolute;
  top: 0;
  left: -45px;
  height: 30px;
  border: none;
  border-left: 1px solid #ddd;
}
ul[data-v-342aec24] {
  list-style-type: none;
  text-align: left;
}
span[data-v-342aec24] {
  display: inline-block;
  text-align: center;
  height: 45px;
  color: #888;
  line-height: 45px;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 0 8px;
  font-size: 1.5em;
  cursor: pointer;
}
/* 获得焦点改变背景色 */
#tree span[data-v-342aec24]:hover,
#tree span:hover + ul span[data-v-342aec24] {
  color: #fff;
  background-color: deepskyblue;
}
/* 获得焦点改变边框和连线的颜色 */
#tree span[data-v-342aec24]:hover,
#tree span:hover + ul span[data-v-342aec24],
#tree span:hover + ul li[data-v-342aec24]:before,
#tree span:hover + ul li[data-v-342aec24]:after {
  border-color: blue;
}

